import Vue from 'vue'
import Router from 'vue-router'

import Home from '@/components/Home'
import Archives from '@/components/Archives'
import Author from '@/components/Author'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      redirect: '/1'
    },
    {
      path: '/:id',
      name: 'Home',
      component: Home
    },
    {
      path: '/archives/:id',
      name: 'Archives',
      component: Archives
    },
    {
      path: '/author',
      name: 'Author',
      component: Author
    }
  ]
})



