<template>
    <div v-loading="loading">
        <div class="content" v-html="content">
        <!-- <mavon-editor v-model="value" @click="dwad" /> -->
        </div>
        <p>这是id为 {{ test }} 的页面</p>
    </div>
</template>

<script>
import marked from 'marked'

export default {
    name: 'Archives',
    data() {
        return {
            content: '',
            test: this.$route.params.id,
            loading: true
        }
    },
    methods: {
        
    },
    created() {
        this.$emit('tooltip', {
            title: 'Back to Index',
            routeName: 'Index'
        });

        this.loading = true;
        this.$axios({
            method: 'GET',
            url: 'https://v1.hitokoto.cn'
        }).then((response) => {
            this.content = marked(response.data.hitokoto);
            this.loading = false;
        });
    }
}
</script>

<style scoped>
    .content {
        margin: 0 auto;
        max-width: 450px;
        text-align: left;
    }
</style>


