<template>
  <div id="app">
    <el-container>
        <el-main>
            <div class="app-body">
                <el-tooltip class="item" effect="dark" :content="tooltip" placement="right">
                  <el-avatar :size="90" :src="circleUrl" class="logo-avatar" @click.native="back()"></el-avatar>
                </el-tooltip>
                <p class="author">Author[#]FlyingQwQ</p>
                <div class="divider"></div>
                <router-view @tooltip="tooltipEvent"/>
            </div>
        </el-main>
        <el-footer>
          <div class="footer">
            <span>&lt;/&gt; Copyright © FlyingQwQ's Blog 2020</span>
          </div>
        </el-footer>
    </el-container>
  </div>
</template>

<script>
import logo from './assets/image/logo.jpg'

export default {
    name: 'App',
    data() {
        return {
            circleUrl: logo,
            tooltip: '测试',
            routeName: ''
        }
    },
    methods: {
        back() {
            this.$router.push({name: this.routeName});
        },
        tooltipEvent(data) {
            this.tooltip = data.title;
            this.routeName = data.routeName;
            
        }
    }
}
</script>

<style>
html, body {
    margin: 0;

    width: 100%;
    height: 100%;
}

#app {
    margin: 0;
    padding-top: 20px;

    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  
    color: #2c3e50;

    text-align: center;
}

.logo-avatar {
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
}

.author {
    font-size: 20px;
    line-height:1.7;
    font-weight: 600;
}

.divider {
    margin: 2em auto;

    background: linear-gradient(to right, rgba(255,255,255,0), #ccc, rgba(255,255,255,0));
    height: 1px;
    max-width: 550px;
}

.footer {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    color: #777;
    font-size: 14px;
}
</style>



