<template>
    <div>
        <div class="content" v-loading="loading">
            <div v-for="(entrance, index) in entrances" :key="index" class="entrance-frame">
                <div class="entrance">
                    <div class="title" @click="jump(entrance.id)">{{ entrance.title }}</div>
                    <div class="post-date">{{ entrance.date == "" ? '2020.2.17' : '2020.2.17' }}</div>
                </div>
                <div class="introduce">{{ entrance.introduce == "" ? 'this is a test theme' : 'this is a test theme' }}</div>
            </div>
        </div>

        <el-pagination layout="prev, pager, next" 
            hide-on-single-page="true"
            :total="archivesnum" 
            :current-page.sync="currentPage" 
            :page-size="5"
            @current-change="currentChange">
        </el-pagination>
    </div>
</template>

<script>


export default {
    name: 'Home',
    data() {
        return {
            archivesnum: 0,
            entrances: [],
            // entrances: [
            //     {id: '1', title: 'Hello, World!', date: '2020.2.17', introduce: 'this is a test theme'},
            //     {id: '2', title: '测试帖子', date: '2020.2.17', introduce: '这是一个测试帖子'},
            //     {id: '3', title: '测试帖子', date: '2020.2.17', introduce: '这是一个测试帖子'},
            //     {id: '4', title: '测试帖子', date: '2020.2.17', introduce: '这是一个测试帖子'},
            //     {id: '5', title: '测试帖子', date: '2020.2.17', introduce: '这是一个测试帖子'}
            // ],
            currentPage: 1,
            loading: true,
        }
    },
    methods: {
        jump(id) {
            this.$router.push({path: '/archives/' + id});
        },

        currentChange() {
            this.dataRefresh('/' + this.currentPage);
        },

        dataRefresh(url) {
            this.$router.push({
                path: url
            });

            this.loading = true;
            this.$axios({
                type: 'GET',
                url: 'https://www.gourouting.com/api/v1/videos?start=' + this.currentPage + '&limit=5'
            }).then((response) => {
                let data = response.data;
                this.archivesnum = data.data.total;
                this.entrances = data.data.items;
                // for(var i = 0; i < data.data.items.length; i++) {
                //     console.info(data.data.items[i]);
                // }
                this.loading = false;
            });
            
        }
    },
    created() {
        this.currentPage = Number(this.$route.params.id);
        
        this.$emit('tooltip', {
            title: 'About author',
            routeName: 'Author'
        });

        this.dataRefresh('/' + this.currentPage);

        // this.$axios({
        //     type: 'GET',
        //     url: 'https://www.gourouting.com/api/v1/videos?start=' + this.currentPage + '&limit=5'
        // }).then((response) => {
        //     let data = response.data;
        //     this.archivesnum = data.data.total;
        //     this.entrances = data.data.items;
        // });
    }
}
</script>

<style scoped>
    .content {
        margin: 0 auto;
        max-width: 450px;
        text-align: left;
    }

    .entrance-frame {
        margin-bottom: 30px;
        position: relative;
    }
    .entrance-frame .entrance {
        margin-bottom: 5px;
        display: flex;
        justify-content: space-between;
        position: relative;
    }
    .entrance-frame .entrance .title {
        position: relative;
        font-size: 18px;
        font-weight: 600;
        border-bottom: 2px solid rgba(0, 0, 0, 0);
        cursor: pointer;
    }
    .entrance-frame .entrance .title:hover {
        border-bottom: 2px solid;
    }

    .entrance-frame .entrance .post-date {
        font-size: 13px;
        color: #757575;
    }
    .entrance-frame .introduce {
        font-size: 13px;
        color: #757575;
    }
</style>


